.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.4;
}

.main-nav--closed .main-nav__toggle:hover::before {
  opacity: 0.7;
}

.main-nav__toggle {
  top: 0;
  right: 0;
  width: 60px;
  height: 55px;
  border: none;
  display: block;
  cursor: pointer;
  z-index: 2;
  background-color: #ffffff;
  margin-left: auto;
  position: relative;
}
.main-nav__toggle:hover::before {
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.6;
}
.main-nav__toggle:active::before {
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 25px;
  height: 4px;
  width: 23px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
  z-index: 10;
}

.main-nav--closed .main-nav__toggle {
  right: 0;
  top: 0;
  background: #81b3d2;
  z-index: 10;
}
.main-nav--closed .main-nav__toggle::before, .main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 26px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--closed .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__toggle:hover::before, .main-nav--closed .main-nav__toggle:hover::after {
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active::before, .main-nav--closed .main-nav__toggle:active::after {
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.3;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs {
  position: static;
  padding-top: 47px;
}

.main-nav--nojs .main-nav__list-item-link {
  display: block;
}

