.form__checkbox {
  display: block;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  margin-right: 16px;
  position: relative;
}

.form__input-checkbox:checked + .form__checkbox::before {
  position: absolute;
  top: 20px;
  left: 20px;
  content: "";
  transform: rotate(130deg);
  background-color: #333333;
  width: 21px;
  height: 3px;
}

.form__input-checkbox:checked + .form__checkbox::after {
  position: absolute;
  top: 22px;
  left: 12px;
  width: 15px;
  height: 3px;
  transform: rotate(45deg);
  background-color: #333;
  content: "";
